  import catalogApi from '@/api/catalogApi'

  export default {
    layout: 'default',
    data() {
      return {
        listProducts: []
      }
    },
    mounted() {
    this.idCate = this.$route.params.idProd
  },



    async created() {
      await this.getAllProducts()
    },

    methods: {
      async getAllProducts() {
        try {
          const {data} = await catalogApi.getProducts(this.$axios)
          this.listProducts = data.products
        } catch (err) {
          console.log(err)
        }
      }
  }
}
